<template>
    <div class='challenge_start'>
        <div class="back" @click.stop="back()">
          <img src="../../assets/image/返回.png" alt />
        </div>
        <div class='challenge_content'>
            <div class='challenge_title'>挑战答题</div>
            <div class='challenge_result'>
                <div class='challenge_failure'>答错离场</div>
                <div class='challenge_success'>答对继续</div>
            </div>
            <div class='challenge_select'>
                <van-picker
                :columns="columns"
                @change="onChange"
                :default-index="selectCode" />
            </div>
            <div class='start_button' @click.stop='startChallenge'>开始答题</div>
            <div class='challenge_getscore'>挑战答题赚积分</div>
        </div>
    </div>
</template>
<script>
import API from '@/api/api'
export default {
  name: 'challenge_start',
  data () {
    return {
      classifyList: [], // 专项数据
      columns: [], // 用于选择的一级专项列表
      specialcode: '', // 专项类型
      selectCode: 0 // 默认选择的code
    }
  },
  created () {
    this.classifyList = JSON.parse(JSON.stringify(this.$store.state.spe.speMap))
    this.columns = this.classifyList.map(obj => { return obj.name })
    this.specialcode = this.classifyList[0].id
  },
  methods: {
    // 返回上一级
    back () {
      this.$router.back()
    },
    // 改变专项时
    onChange (picker, value, index) {
      this.specialcode = this.classifyList[index].id
    //   Toast(`当前值：${value}, 当前索引：${index}`)
    },
    startChallenge () {
      API.post('challenge', {
        specialCode: this.specialcode
      }, 'json', (data) => {
        if (data.success) {
          this.$router.push({ // 进入随机练习
            name: 'challenge_answer',
            params: {
              specialCode: this.specialcode
            }
          })
        } else {
          this.$toast(data.msg)
        }
      })
    }
  }
}
</script>
<style scoped>
    .challenge_start{
        width:100vw;
        height:100vh;
        background: url('../../assets/image/挑战答题背景.png');
        background-size: 100% 100%;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }
    .challenge_start .back {
        width: 0.89rem;
        height: 0.94rem;
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 99;
    }
    .back img {
        width: 0.19rem;
        height: 0.34rem;
        display: block;
    }
    .challenge_content{
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top:1.44rem;
    }
    .challenge_title{
        font-size:1.3rem;
        font-family:Source Han Sans CN;
        font-weight:bold;
        color:rgba(255,255,255,1);
        text-shadow:0px 0.03rem 0.06rem rgba(63,88,77,0.56);
    }
    .challenge_result{
        margin-top:0.4rem;
        width:4.16rem;
        height:0.8rem;
        position: relative;
    }
    .challenge_result div{
        background: rgba(67,134,241,0.85);
        color:#fff;
        display: flex;
        align-items: center;
        justify-content: center;
        width:2rem;
        height:0.6rem;
        font-size:0.36rem;
        font-family:Source Han Sans CN;
    }
    .challenge_result .challenge_failure{
        position: absolute;
        left:0;
        top:0;
        border-radius:0px 0.16rem 0px 0.16rem;
    }
    .challenge_result .challenge_success{
        position: absolute;
        right:0;
        bottom:0;
        border-radius:0px 0.16rem 0px 0.16rem;
    }
    .challenge_select{
        width:5.84rem;
        margin-top:0.6rem;
    }
    .start_button{
        margin-top:0.68rem;
        width:3.65rem;
        height:0.98rem;
        border-radius: 0.54rem;
        background:rgba(255,255,255,1);
        color:#5794F6;
        font-size:0.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .challenge_getscore{
        margin-top:0.32rem;
        font-size:0.32rem;
        font-family:Source Han Sans CN;
        color:rgba(255,255,255,0.86);
    }
</style>
<style lang="stylus">
    .challenge_start .van-picker{
        background:rgba(85,204,152,0)
    }
    .challenge_start .van-picker__mask{
        background-image:none !important;
    }
    .challenge_start .van-picker-column__item--selected{
        background:rgba(49,121,238,0.12);
        border:none !important;
    }
    .challenge_start  .van-picker-column__item{
        color:#fff;
        font-size:0.4rem;
    }
</style>



